<template lang="html">
  <div class="dxyk-body-main-module1">
    <ul class="dxyk-body-main-shares">
      <li v-for="share in getShares">
        <span class="dxyk-share-time">{{share.time}}</span>
        <span class="dxyk-share-name" :title="share.name">{{share.name}}</span>
      </li>
    </ul>
    <page-navigator
      :currentPage="2"
      :maxPage="maxPage"
      :handlePageClick="handlePageClick"
    />
  </div>
</template>

<script>
import pageNavigator from './page-navigator'

export default {
  name: 'dxyk-module1',
  props: {
    shares: {
      type: Array,
      required: true
    },
    handlePageClick: {
      type: Function,
      required: true
    }
  },
  created () {
    this.pageSize = 12
    this.currentPage = 1
    const length = this.shares.length
    const maxPage = Math.ceil(length / this.pageSize)
    this.maxPage = maxPage
  },
  data () {
    return {
      currentPage: null,
      maxPage: null,
      pageSize: null
    }
  },
  computed: {
    getShares () {
      return this.shares.slice(0, 11)
    }
  },
  components: {
    pageNavigator
  }
}
</script>

<style lang="scss">
@import "../../../styles/mixin.scss";

.dxyk-body-main-module1 {
  float: left;
}

.dxyk-body-main-shares {
  & li {
    margin-top: 26px;
    @include flexAlignCenter;
  }

  & li:first-child {
    margin-top: 0;
  }
}

.dxyk-share-time {
  font-size: 14px;
  color: #adadad;
}

.dxyk-share-name {
  position: relative;
  font-size: 16px;
  color: #666666;
  margin-left: 20px;
  padding-left: 12px;
  overflow: hidden;
  text-overflow:ellipsis;
  white-space: nowrap;
  vertical-align: middle;
  @include pointer;
  &:before {
    @include beforeCircle(6px);
  }

  &:hover {
    color: #2caa3f;
  }

  &:hover:before {
    background-color: #2caa3f;
  }
}


</style>



