<template lang="html">
  <div class="dxyk-body-main-module2">
    <div class="dxyk-body-main-module2-d1">
      <header class="dxyk-body-main-module2-header">
        <div class="dxyk-body-main-module2-header-d1">
          <span>今日推荐</span>
        </div>
        <div class="dxyk-body-main-module2-header-d2">
          <span>{{getTodayDayString}}</span>
          <span>{{getTodayWeekDayString}}</span>
        </div>
      </header>
      <div class="dxyk-body-main-module2-main">
        <div v-for="share in hotShares" :key="share.id" class="dxyk-body-main-module2-main-share">
          <div class="dxyk-body-main-module2-main-share-time">
            <span>{{share.time}}</span>
          </div>
          <div class="dxyk-body-main-module2-main-share-name">
            <span :title="share.name">{{share.name}}</span>
          </div>
        </div>
      </div>
    </div>
    <div class="dxyk-body-main-module2-d2" />
    <div class="dxyk-body-main-module2-d3" />
  </div>
</template>

<script>
export default {
  name: 'dxyk-module2',
  props: {
    timeObject: {
      type: Object,
      required: true
    },
    hotShares: {
      type: Array,
      required: true
    }
  },
  computed: {
    getTodayDayString () {
      const timeObject = this.timeObject
      const todayDayString = `${timeObject.month}/${timeObject.day}`
      return todayDayString
    },
    getTodayWeekDayString () {
      const timeObject = this.timeObject
      const weekDay = timeObject.weekDay
      let weekDayString
      switch (weekDay) {
        case 0:
          weekDayString = '日'
          break
        case 1:
          weekDayString = '一'
          break
        case 2:
          weekDayString = '二'
          break
        case 3:
          weekDayString = '三'
          break
        case 4:
          weekDayString = '四'
          break
        case 5:
          weekDayString = '五'
          break
        case 6:
        default:
          weekDayString = '六'
          break
      }
      weekDayString = '星期' + weekDayString
      return weekDayString
    }
  }
}
</script>

<style lang="scss">
@import "../../../styles/mixin.scss";
@import "../../../styles/variable.scss";

$dw: 4px;

.dxyk-body-main-module2 {
  float: right;
  width: 250px;
  height: 100%;
  position: relative;
}

.dxyk-body-main-module2-d1, .dxyk-body-main-module2-d2, .dxyk-body-main-module2-d3 {
  position: absolute;
  border: 1px solid #eeeeee;
  width: 240px;
  height: 250px;
  background-color: #fff;
}

.dxyk-body-main-module2-d1 {
  z-index: 3;
  top: 0;
  left: 0;
  padding-top: 20px;
}

.dxyk-body-main-module2-d2 {
  z-index: 2;
  top: 1 * $dw;
  left: 1 * $dw;
}

.dxyk-body-main-module2-d3 {
  z-index: 1;
  top: 2 * $dw;
  left: 2 * $dw;
}

.dxyk-body-main-module2-header {
  @include flexAlignCenter;
  justify-content: space-between;
  padding-right: 20px;

  @at-root {
    .dxyk-body-main-module2-header-d1 {
      width: 90px;
      font-family: $base-font-family3;
      background-color: #2caa3f;
      color: #fff;
      height: 36px;
      line-height: 36px;
      text-align: center;
      border-top-right-radius: 20px;
      border-bottom-right-radius: 20px;
    }

    .dxyk-body-main-module2-header-d2 {
      font-size: 12px;
      color: #999999;
    }
  }
}

.dxyk-body-main-module2-main-share {
  margin-top: 14px;
  padding:0 14px;

  @at-root {
    .dxyk-body-main-module2-main-share-time {
      color: #adadad;
      font-size: 12px;
    }

    .dxyk-body-main-module2-main-share-name {
      position: relative;
      margin-top: 8px;
      color: #666666;
      font-size: 14px;
      vertical-align: middle;
      overflow: hidden;
      text-overflow:ellipsis;
      white-space: nowrap;
      padding-left: 12px;
      & span {
        @include pointer;
        &:hover {
          color: #2caa3f;
        }
      }
      &:before {
        @include beforeCircle(6px, #ff9a41);
        @include pointer;
      }
      &:hover:before {
        background-color: #2caa3f;
      }
    }
  }
}

</style>



