<template lang="html">
  <div class="page-navigator">
    <span class="page-pre" @click="onPrePageClick"></span>
    <span v-if="!isContainFirstPage" class="page page-unactive" @click="onPageClick(1)">1</span>
    <span v-if="!isContainFirstPage" class="diandiandian">...</span>
    <span
      v-for="(p, index) in pages"
      :key="index"
      class="page"
      :class="{'page-active': p === currentPage, 'page-unactive': p !== currentPage}"
      @click="onPageClick(p)"
    >
      {{p}}
    </span>
    <span v-if="!isContainMaxPage" class="diandiandian">...</span>
    <span v-if="!isContainMaxPage" class="page page-unactive" @click="onPageClick(lastPage)">{{lastPage}}</span>
    <span class="page-next" @click="onNextPageClick"></span>
  </div>
</template>

<script>
export default {
  name: 'page-navigator',
  props: {
    currentPage: {
      type: Number,
      required: true
    },
    maxPage: {
      type: Number,
      required: true
    },
    handlePageClick: {
      type: Function,
      required: true
    }
  },
  data () {
    return {
      pages: null
    }
  },
  created () {
    this.pages = this.calcPages()
  },
  computed: {
    isContainFirstPage () {
      const firstPage = this.pages[0]
      return firstPage === 1
    },
    isContainMaxPage () {
      const lastPage = this.pages[this.pages.length - 1]
      return lastPage === this.maxPage
    },
    lastPage () {
      return this.maxPage
    }
  },
  methods: {
    calcPages () {
      let pageArray = []
      let currentPage = this.currentPage
      let pageCount = 5
      let maxPage = this.maxPage
      var halfPageCount = parseInt(pageCount / 2)
      if (maxPage > pageCount) {
        let delta = maxPage - currentPage
        if (currentPage >= (halfPageCount + 1) && delta >= halfPageCount) {
          for (let j = 0; j < halfPageCount; j++) {
            pageArray.push(currentPage - (halfPageCount - j))
          }
          pageArray.push(currentPage)
          for (let k = 1; k <= halfPageCount; k++) {
            pageArray.push(currentPage + k)
          }
        } else if (currentPage <= (halfPageCount + 1)) {
          for (let i = 1; i <= pageCount; i++) {
            pageArray.push(i)
          }
        } else if (delta < halfPageCount) {
          for (let i = (pageCount - delta - 1); i > 0; i--) {
            pageArray.push(currentPage - i)
          }
          pageArray.push(currentPage)
          for (let i = 1; i <= delta; i++) {
            pageArray.push(currentPage + i)
          }
        }
      } else {
        for (let j = 1; j <= maxPage; j++) {
          pageArray.push(j)
        }
      }
      return pageArray
    },
    onPageClick (page) {
      const currentPage = this.currentPage
      if (currentPage !== page) {
        console.log(page)
      }
    },
    onPrePageClick () {
      const currentPage = this.currentPage
      if (currentPage > 1) {
        console.log(currentPage - 1)
      }
    },
    onNextPageClick () {
      const currentPage = this.currentPage
      const maxPage = this.maxPage
      if (currentPage < maxPage) {
        console.log(currentPage + 1)
      }
    }
  }
}
</script>

<style lang="scss">
@import '../../../styles/mixin.scss';

$w: 24px;
$ml: 30px;
$activeColor: #2caa3f;

.page-navigator {
  margin-top: 50px;
  @include flexAlignCenter;
}

.page-pre, .page-next, .page {
  width: $w;
  height: $w;
  display: inline-block;
  @include pointer;
}

.page-pre, .page-next {
  background-size: 100% 100%;
}

.page-pre {
  background: url('../../../assets/imgs/dxyk/arrow-left.png') no-repeat;
}

.page-next {
  background: url('../../../assets/imgs/dxyk/arrow-right.png') no-repeat;
  margin-left: $ml;
}

.page {
  text-align: center;
  line-height: $w;
  font-size: 14px;
  border-radius: 50%;

  &:nth-child(2) {
    margin-left: $ml;
  }
}

.page, .diandiandian {
  margin-left: 20px;
}

.page-unactive {
  border: 1px solid #fff;
  color: #666666;
  &:hover {
    border-color: $activeColor;
    color: $activeColor;
  }
}

.page-active {
  color: #fff;
  background-color: $activeColor;
}

</style>



