<template lang="html">
  <div class="dxyk-container">
    <dxyk-header></dxyk-header>
    <dxyk-body
      :timeObject="timeObject"
      :shares="shares"
      :jobs="jobs"
      :hotShares="hotShares"
      :handlePageClick="onPageClick"
    />
  </div>
</template>

<script>
import DateUtil from 'kmkm-utils/dist/DateUtil'
import dxykHeader from './components/dxyk-header'
import dxykBody from './components/dxyk-body'
import { shares, jobs, hotShares } from '@service/analog-data'

export default {
  data () {
    return {
      timeObject: null,
      shares,
      jobs,
      hotShares
    }
  },
  created () {
    this.timeObject = DateUtil.getCurrentTimeObject()
  },
  methods: {
    onPageClick (page) {
      console.log(page)
    }
  },
  components: {
    dxykBody,
    dxykHeader
  }
}
</script>

<style lang="scss">
@import "../../styles/mixin.scss";

.dxyk-container {
  background-color: #f6f6f6;
  background-image: url('../../assets/imgs/dxyk/bkg.png');
  background-repeat: no-repeat;
  background-size: 100%;
  font-family: $base-font-family;
}
</style>



