<template lang="html">
  <div class="dxyk-body">
    <header class="dxyk-body-header">
      <div class="dxyk-filter">
        <span v-for="job in jobs">{{job.name}}</span>
      </div>
      <div class="dxyk-search">
        <input placeholder="输入搜索内容"/>
        <i class="dxyk-search-icon"></i>
      </div>
    </header>
    <div class="dxyk-body-main clearfix">
      <dxyk-module1
        :shares="shares"
        :handlePageClick="handlePageClick"
      />
      <dxykModule2
        :timeObject="timeObject"
        :hotShares="hotShares"
      />
    </div>
  </div>
</template>

<script>
import dxykModule1 from './dxyk-module1'
import dxykModule2 from './dxyk-module2'

export default {
  name: 'dxyk-body',
  props: {
    timeObject: {
      type: Object,
      required: true
    },
    shares: {
      type: Array,
      required: true
    },
    jobs: {
      type: Array,
      required: true
    },
    hotShares: {
      type: Array,
      required: true
    },
    handlePageClick: {
      type: Function,
      required: true
    }
  },
  components: {
    dxykModule1,
    dxykModule2
  }
}
</script>

<style lang="scss">
@import "../../../styles/mixin.scss";
@import "../../../styles/variable.scss";

.dxyk-body {
  margin: 40px auto;
  background-color: #fff;
  width: 80%;
  padding: 56px 80px;
}

.dxyk-body-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.dxyk-filter {
  & span {
    display: inline-block;
    color: #c0c0c0;
    border: 1px solid #c0c0c0;
    border-radius: 20px;
    padding: 8px 16px;
    font-size: 16px;
    margin-left: 20px;
    @include pointer;
  }

  & span:first-child {
    margin-left: 0;
  }

  & span:hover {
    color: #72c67f;
    border-color: #72c67f;
  }
}

.dxyk-search {
  width: 200px;
  position: relative;
  & input {
    width: 100%;
    border: 1px solid #eeeeee;
    padding:10px 40px 10px 14px;
    border-radius: 20px;
    outline: none;
  }

  & input:focus {
    border-color: #cccccc;
  }

  @at-root {
    .dxyk-search-icon {
      position: absolute;
      right: 0;
      top: 0;
      height: 100%;
      width: 50px;
      background: url('../../../assets/imgs/dxyk/search.png') no-repeat;
      background-position: center center;
      @include pointer;
    }
  }
}

.dxyk-body-main {
  $top: 30px;
  margin-top: $top;
  padding-top: $top + 10px;
  border-top: 1px solid #eeeeee;
}

</style>



